.output{
    margin: 0.5rem 0.5rem 0.5rem 0.5rem;
    padding-top: 1rem;
    height: 8rem;
    border: 2px solid;
    justify-content: space-between;
    align-items: center;
}

.buttons{
    display: flex;
    justify-content:flex-end;
    margin-right: 4rem;
}

.reset {
    padding: 1rem 1rem 1rem 1rem;
    border: 0 solid;
    border-radius :50%;
    background-image: url(../../image/reset.png);
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 1rem;
}

.kakao{
    padding: 1rem 1rem 1rem 1rem;
    border: 0 solid;
    border-radius :50%;
    background-image: url(../../image/icon-kakao.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
    margin-left: 1rem;
}

.list{
    float: left;
    padding: 1rem;
}


.bonus{
    padding: 1rem;
    overflow: hidden;
}